    import { mapState } from 'vuex'
	import { getHomeList } from '../../api/mainApi.js'
    export default {
		data() {
			return {
				homeList:{},
				banner:[], // 轮播图
				navs:[],   // 导航nav
				advs:[],   // 广告
				seles:[],  // 精选
				indicatorDots: false,
				autoplay: true,
				interval: 3000,
				duration: 500,
			}
		},
        computed: mapState(['forcedLogin', 'hasLogin', 'userName']),
        onLoad() {
			
        },
		onShow() {
			// 获取首页banner
			this.getHomeList()
			
		},
		methods:{
			// 去广告页面
			goadSet(item){
				if (item.type == 1) {
					uni.navigateTo({
						url:'/pages/common/webview/webview?url='+ item.url
					})
				}else if(item.type == 5){
					uni.navigateTo({
						url:'/pages/order/goodsDetail/goodsDetail/'+item.id +'/'+item.url
					})
				}
			},
			// nav 去搜索页面
			goSearchPage(name){
				uni.navigateTo({
					url:'/pages/order/goodsList/goodsList?search='+name
				})
			},
			// 去搜索页面
			goSearch(){
				uni.navigateTo({
					url:'/pages/main/search/search'
				})
			},
			// 获取首页banner
			getHomeList(){
				getHomeList({ parentId: 1 }).then((res) => {
					if(res.code == '1000'){
						this.homeList = res.data
					}
				})
			},
			goNextPage(url){
				if(url){
					uni.navigateTo({
					    url
					});
				}
			},
			goGoodsDetail(shopId,goodsId){
				uni.navigateTo({
					url:'/pages/order/goodsDetail/goodsDetail?shopId='+shopId + '&goodsId='+goodsId
				})
			}
		}
    }
